.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0; }

.sr-only-focusable:active, .sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto; }

/* ==|====================
   Component / Theme-Switcher
   ======================= */
.theme-switcher {
  float: right;
  height: 4rem; }

.theme-switcher__label {
  display: block;
  float: left;
  font-size: 0.875rem;
  line-height: 4.57143;
  margin-right: 1.25rem; }

.theme-switcher__list {
  display: inline-block;
  list-style-type: none;
  padding: 0;
  margin: 0;
  float: right; }

.theme-switcher__item {
  display: block;
  float: left;
  padding-top: 1.25rem;
  padding-bottom: 1.25rem; }
  .theme-switcher__item:not(:last-child) {
    margin-right: 0.625rem; }

.theme-switcher__trigger {
  position: relative;
  display: block;
  height: 1.5rem;
  width: 1.5rem;
  border: 1px solid #484848;
  border-radius: 50%;
  transition: border .22s ease; }
  .theme-switcher__trigger:after {
    content: " ";
    pointer-events: none;
    position: absolute;
    bottom: -0.5rem;
    transform: translateY(0.625rem);
    height: 0.125rem;
    background-color: transparent;
    width: 100%;
    cursor: default;
    transition: transform .22s ease-in-out, background-color .22s ease-in-out; }
  .theme-switcher__trigger.is-dark {
    background-color: #222222; }
  .theme-switcher__trigger.is-light {
    background-color: white; }
  .theme-switcher__trigger.is-active:after {
    transform: translateY(0);
    background-color: var(--color-secondary); }

/* ==|====================
   Component / Pagetitle
   ======================= */
.pagetitle {
  font-size: 3rem;
  line-height: 1.16667;
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  color: var(--font-color-light);
  font-weight: 300;
  letter-spacing: -0.05rem;
  width: 60%;
  margin-top: 4.375rem;
  margin-bottom: 3.125rem; }
  .pagetitle__highlight {
    color: var(--color-primary);
    font-weight: 500; }

/* ==|====================
   Component / Filter
   ======================= */
.filter {
  list-style-type: none;
  padding: 0;
  margin: 0; }
  .filter__item {
    display: inline-block;
    margin-right: var(--spacer); }
  .filter__label, .filter__trigger {
    display: block;
    font-size: 0.75rem;
    line-height: 1.33333;
    letter-spacing: .4px;
    padding-top: 0.75rem;
    padding-bottom: 0.75rem; }
  .filter__trigger {
    text-transform: uppercase;
    font-weight: 500;
    cursor: pointer;
    border-bottom: 2px solid transparent;
    transition: border-color .22s ease-in-out, color .22s ease-in-out;
    color: inherit;
    text-decoration: none; }
    .filter__trigger:hover, .filter__trigger:focus, .filter__trigger:active {
      color: var(--color-secondary);
      text-decoration: none; }
    .filter__trigger.is-active {
      color: var(--color-secondary);
      border-color: var(--color-secondary); }

/* ==|====================
   Component / Button
   ======================= */
.btn {
  display: block;
  font-size: 0.75rem;
  line-height: 1.9375rem;
  border-radius: 0.1875rem;
  border: 0;
  background-color: var(--color-primary);
  color: white;
  height: 2rem;
  padding-left: 0.9375rem;
  padding-right: 0.9375rem;
  text-transform: uppercase;
  font-weight: 600;
  transition: background-color .22s ease-in-out;
  cursor: pointer; }
  .btn:hover, .btn:active, .btn:focus {
    background-color: var(--color-primary-darken);
    outline: none; }

/* ==|====================
   Component / Action Bar
   ======================= */
.action-bar {
  --spacer: 2rem;
  border-bottom: 1px solid var(--color-borders);
  overflow: hidden; }
  .action-bar .filter {
    float: left; }
  .action-bar .btn,
  .action-bar .show-next {
    float: right; }
  .action-bar .btn {
    margin-top: 0.1875rem; }
  .action-bar .show-next {
    margin-right: var(--spacer); }

/* ==|====================
   Component / Show Next
   ======================= */
.show-next__checkbox {
  margin-right: 0.3125rem; }

.show-next__label {
  display: inline-block;
  font-size: 0.75rem;
  line-height: 1.33333;
  letter-spacing: .4px;
  padding-top: 0.75rem;
  padding-bottom: 0.75rem;
  text-transform: uppercase;
  font-weight: 500;
  cursor: pointer;
  border-bottom: 2px solid transparent;
  transition: border-color .22s ease-in-out;
  color: inherit;
  text-decoration: none; }

/* ==|====================
   Component / Notelist
   ======================= */
.notelist {
  padding: 0;
  margin: 1.25rem 0 0;
  list-style-type: none; }

/* ==|====================
   Component / Note
   ======================= */
.note {
  display: flex;
  padding: 1.5rem 0;
  transition: padding .22s ease-in-out; }
  .note:not(:last-child) {
    border-bottom: 1px solid var(--color-borders); }
  .note__action {
    flex: 0 0 1.875rem; }
  .note__content {
    flex: 1 1 auto;
    position: relative; }
  .note__meta {
    flex: 0 0 15.625rem;
    text-align: right;
    font-size: 0.875rem;
    line-height: 1.78571; }
  .note__finish-checkbox {
    position: relative;
    top: 0.1875rem; }
  .note__title {
    font-size: 1.5rem;
    line-height: 1.16667;
    letter-spacing: -0.025rem;
    color: var(--font-color-light);
    font-weight: 600;
    text-overflow: ellipsis;
    margin: 0 0 0.9375rem; }
  .note__description {
    margin: 0; }
  .note__finishdate {
    position: absolute;
    bottom: -20px;
    font-size: 0.75rem;
    font-weight: 300;
    opacity: 0;
    transition: opacity .22s ease-in-out; }
  .note__edit-link {
    position: relative;
    font-size: 0.75rem;
    font-weight: 500;
    color: inherit;
    text-decoration: none;
    text-transform: uppercase;
    transition: color .15s ease-in-out; }
    .note__edit-link:after {
      content: " ";
      pointer-events: none;
      position: absolute;
      left: 0;
      bottom: -0.375rem;
      transform: translateY(0.625rem);
      height: 0.125rem;
      background-color: transparent;
      width: 100%;
      cursor: default;
      transition: transform .15s ease-in-out, background-color .15s ease-in-out; }
    .note__edit-link:hover {
      color: var(--color-secondary); }
      .note__edit-link:hover:after {
        transform: translateY(0);
        background-color: var(--color-secondary); }
  .note.is-finished {
    padding-bottom: 2.5rem; }
    .note.is-finished .note__finishdate {
      opacity: 1; }
